:root {
  --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
}

:root {
  --blue: hsl(211, 100%, 50%);
  --indigo: hsl(263, 90%, 51%);
  --purple: hsl(261, 51%, 51%);
  --pink: hsl(332, 79%, 58%);
  --red: hsl(354, 70%, 54%);
  --orange: hsl(27, 98%, 54%);
  --yellow: hsl(45, 100%, 51%);
  --green: hsl(134, 61%, 41%);
  --teal: hsl(162, 73%, 46%);
  --cyan: hsl(189, 100%, 48%);
  --white: hsl(0, 0%, 100%);
  --gray: hsl(208, 7%, 46%);
  --gray-dark: hsl(210, 10%, 23%);
  --gray-dark-1: hsl(210, 11%, 15%);
  --gray-dark-2: hsl(0, 0%, 33%);
  --primary: hsl(211, 100%, 50%);
  --secondary: hsl(208, 7%, 46%);
  --success: hsl(134, 61%, 41%);
  --info: hsl(188, 78%, 41%);
  --warning: hsl(45, 100%, 51%);
  --danger: hsl(354, 70%, 54%);
  --light: hsl(210, 17%, 98%);
  --dark: hsl(210, 10%, 23%);
  --darkbackground: hsla(0, 0%, 0%, 0.7);
  --darkdark: hsl(0, 0%, 0%);
  --bluefair: hsl(191, 92%, 54%);
  --grayfair: hsl(204, 22%, 91%);
  --graydark: hsl(0, 0%, 44%);
  --white: #fff;
  --gray: #707070;
  --gray--secondary: #e2e8ec;
  --primary: #1ccef5;
  --dark: #000;
  --dark--secondary: #262626;
  --margin-bottom-title: 1.9rem;
  --margin-top-title: 6rem;
}

