.slider {
  width: 100%;
  height: 31.25rem;
  overflow: hidden;
  /*css for manual slide navigation*/
  /*css for automatic navigation*/
}
.slider .slides {
  width: 500%;
  height: 31.25rem;
  display: flex;
}
.slider .slides input {
  display: none;
}
.slider .slide {
  width: 20%;
  transition: 2s;
  position: relative;
}
.slider .slide__content {
  position: absolute;
  top: 35%;
  padding: 3.75rem 0 3.75rem 3.125rem;
}
.slider .slide__content h3 {
  text-transform: uppercase;
  font-size: 1.125rem;
  opacity: 0.8;
  font-weight: 600;
  margin-bottom: 0.625rem;
  color: var(--white);
}
.slider .slide__content p {
  text-transform: uppercase;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 1.8rem;
  color: var(--white);
}
.slider .slide img {
  width: 100%;
  height: 25rem;
}
.slider .navigation-manual {
  position: absolute;
  width: 100%;
  margin-top: -2.5rem;
  display: flex;
  justify-content: center;
}
.slider .manual-btn {
  border: 2px solid var(--grayfair);
  padding: 0.3125rem;
  border-radius: 0.625rem;
  cursor: pointer;
  transition: 1s;
}
.slider .manual-btn:not(:last-child) {
  margin-right: 2.5rem;
}
.slider .manual-btn:hover {
  background: var(--darkdark);
}
.slider #radio1:checked ~ .first {
  margin-left: 0;
}
.slider #radio2:checked ~ .first {
  margin-left: -20%;
}
.slider #radio3:checked ~ .first {
  margin-left: -40%;
}
.slider #radio4:checked ~ .first {
  margin-left: -60%;
}
.slider .navigation-auto {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 28.75rem;
}
.slider .navigation-auto div {
  border: 0.125rem solid var(--grayfair);
  padding: 0.3125rem;
  border-radius: 0.625rem;
  transition: 1s;
}
.slider .navigation-auto div:not(:last-child) {
  margin-right: 2.5rem;
}
.slider #radio1:checked ~ .navigation-auto .auto-btn1 {
  background: var(--darkdark);
}
.slider #radio2:checked ~ .navigation-auto .auto-btn2 {
  background: var(--darkdark);
}
.slider #radio3:checked ~ .navigation-auto .auto-btn3 {
  background: var(--darkdark);
}
.slider #radio4:checked ~ .navigation-auto .auto-btn4 {
  background: var(--darkdark);
}

